/* _layout.css */

.calculator {
  border: 1px solid var(--blue);
  margin: 0 auto;
  max-width: 100%;
  padding: 4rem 2rem;
  position: relative;
  transition-duration: 0.3s;
  transition-property: border, width, height;
  width: 600px;
}

.calculator__title {
  background-color: var(--blue);
  color: white;
  display: inline-block;
  font-size: 1.5rem;
  left: 50%;
  line-height: 1;
  margin: 0;
  min-width: 240px;
  padding: 1.5rem 2rem;
  position: absolute;
  text-align: center;
  transform: translate(-50%, 0);
  top: -2rem;
}

.calculator--submitted {
  width: 800px;
}

.calculator__results {
  display: none;
}

.calculator--submitted .calculator__results {
  display: block;
  margin-top: 4rem;
}

@media (min-width: 500px) {
  .calculator__title {
    left: initial;
    transform: translate(0, 0);
  }
}

@media (min-width: 700px) {
  .calculator--submitted .calculator__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 4rem;
  }

  .calculator--submitted .calculator__results {
    margin-top: 0;
  }
}

.calculator__sharing {
  text-align: center;
}

.calculator__sharing__title {
  margin-bottom: 2rem;
}

.calculator__sharing button:first-of-type {
  margin-right: 6rem;
}
