/* _buttons.css */

.calculator button,
.calculator a.btn {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: var(--btn--default);
  border: 1px solid var(--btn--default);
  color: #fff;
  font-family: var(--font-family--sans-serif);
  font-size: 16px;
  height: 3.8rem;
  line-height: var(--line-height--input);
  margin-bottom: 0;
  margin-top: 0;
  padding: 10px 15px;
  text-decoration: none;
}

.calculator button::-moz-focus-inner {
  border: 0;
}

.calculator button:active {
  background-color: var(--btn--active);
  box-shadow: var(--box-shadow--active);
}

.calculator button:focus {
  box-shadow: var(--box-shadow--focus);
  outline-width: 0;
}

.calculator button:focus:active {
  box-shadow: var(--box-shadow--focus), var(--box-shadow--active);
}

/* Button with svg icon */
.calculator .btn--icon,
.calculator .btn.btn--icon {
  align-items: center;
  display: flex;
  font-size: 1.4rem;
  height: 4rem;
  justify-content: center;
  width: 150px;
}

.calculator .btn--icon svg {
  height: 18px;
  margin-right: 10px;
}

.calculator .btn--icon path {
  fill: #fff;
}

/* Small round info button toggles input details drawer */
.calculator .btn--toggle {
  background-color: #fff;
  border-radius: 100%;
  color: var(--blue);
  height: var(--rangeslider--thumb-diameter);
  font-family: var(--font-family--serif);
  font-size: 12px;
  font-weight: 700;
  padding: 3px;
  text-align: center;
  width: var(--rangeslider--thumb-diameter);
}

.calculator .btn--toggle:active {
  background-color: var(--btn--active);
  color: #fff;
}

.calculator .btn--switch {
  --height: 14px;
  --top: -4px;

  background-color: var(--lt-gray);
  border: 1px solid var(--blue);
  border-radius: 10px;
  cursor: pointer;
  height: var(--height);
  margin: auto;
  padding: 0;
  position: relative;
  transition-delay: 0.21s;
  transition-duration: 0.04s;
  transition-property: background-color;
  width: 32px;
}

.calculator .control__input.btn--switch {
  flex-grow: 0;
}

.calculator .btn--switch:active {
  box-shadow: none;
}

.calculator .btn--switch::after {
  background-color: #fff;
  border: 1px solid var(--blue);
  border-radius: 50%;
  content: "";
  height: var(--rangeslider--thumb-diameter);
  left: -5px;
  position: absolute;
  right: unset;
  transform: translateY(var(--translateY));
  top: var(--top);
  transition-duration: 0.2s;
  transition-property: left;
  width: var(--rangeslider--thumb-diameter);
}

.calculator .btn--switch:focus::after {
  box-shadow: 0 0 0 3px var(--lt-blue);
}

.calculator .btn--switch[aria-checked=true] {
  background-color: var(--blue);
}

.calculator .btn--switch[aria-checked=true]::after {
  left: calc(var(--rangeslider--thumb-diameter) - 5px);
}
