/* _forms.css */

form {
  display: block;
  font-family: var(--font-family--sans-serif);
}

.calculator label,
.calculator .control__label {
  font-size: 1.4rem;
  margin: 0;
  padding: 0;
}

/* State */
.message--error {
  color: var(--red);
}

/* input groups */
fieldset {
  border: none;
  padding: 0;
}

.input-group {
  padding-bottom: 3rem;
}

/* button groups */
.btn-group {
  display: flex;
  justify-content: space-around;
}

/* input control groups */
.control,
.control--row {
  display: flex;
  flex-wrap: wrap;
}

.control {
  padding-bottom: 2rem;
}

.control--row {
  align-items: center;
  justify-content: space-between;
  padding-bottom: 2rem;
}

.control--row:last-of-type {
  padding-bottom: 0;
}

.input-group > :last-child {
  padding-bottom: 0;
}

.control--row > * {
  margin: 0;
}

.calculator .control__label {
  order: 0;
  width: 85%;
}

.control__btn {
  order: 1;
  margin-left: auto;
  text-align: center;
}

.control__input {
  flex: 1 0 auto;
  order: 2;
  width: 100%;
}

.control__drawer {
  align-self: flex-end;
  flex: 1 0 auto;
  order: 3;
  width: 100%;
}

.calculator .control--row .control__label {
  margin-right: 2rem;
  width: 60%;
}

.control--row .control__btn {
  margin-left: auto;
  order: 2;
}

.control--row .control__input {
  flex: 1 1 auto;
  order: 1;
}
