/* _range.css */

/**
 * Most range styles set by vendor prefix
 * Components have to be set individually
 * See https://css-tricks.com/sliding-nightmare-understanding-range-input/
 */

/* Range track */
@define-mixin range-track {
  background-color: var(--rangeslider--track-color);
  border: none;
  box-sizing: border-box;
  height: var(--rangeslider--track-height);
  width: 100%;
}

/* Range progress */
@define-mixin range-progress {
  background-color: var(--rangeslider--fill-color);
  height: var(--rangeslider--track-height);
}

/* Range thumb */
@define-mixin range-thumb {
  background-color: var(--rangeslider--thumb-color);
  border: none;
  border-radius: 50%;
  height: var(--rangeslider--thumb-diameter);
  width: var(--rangeslider--thumb-diameter);
}

/* Range thumb:focus */
@define-mixin range-thumb--focus {
  box-shadow: 0 0 0 3px var(--lt-blue);
}

input[type=range] {
  appearance: none;
  background-color: transparent;
  font-family: var(--font-family--system-ui);
  font-size: var(--font-size--input);
  height: var(--rangeslider--thumb-diameter);
  margin: 0;
  padding: calc(var(--rangeslider--thumb-diameter) * 2) 0 5px;
  width: 100%;
}

input[type=range]::-moz-focus-outer {
  border: 0;
}

input[type=range]:focus {
  outline-width: 0;
}

/* Webkit track */
input[type=range]::-webkit-slider-runnable-track {
  @mixin range-track;

  /**
   * Use gradient to set progress color. Requires js
   */
  --range: calc(var(--max) - var(--min));
  --ratio: calc((var(--val) - var(--min)) / var(--range));
  --sx: calc(0.5 * var(--rangeslider--thumb-diameter) + var(--ratio) * (100% - var(--rangeslider--thumb-diameter)));

  background:
    linear-gradient(var(--rangeslider--fill-color), var(--rangeslider--fill-color))
    0 / var(--sx) 100% no-repeat var(--rangeslider--track-color);
}

/* Webkit track .no-js fallback */
.no-js input[type=range]::-webkit-slider-runnable-track {
  background: var(--rangeslider--track-color) !important;
}

/* Webkit thumb */
input[type=range]::-webkit-slider-thumb {
  margin-top: calc(0.5 * calc(var(--rangeslider--track-height) - var(--rangeslider--thumb-diameter)));

  @mixin range-thumb;

  -webkit-appearance: none;
}

/* Webkit thumb:focus */
input[type=range]:focus::-webkit-slider-thumb {
  @mixin range-thumb--focus;
}

/* Mozilla track */
input[type=range]::-moz-range-track {
  @mixin range-track;
}

/* Mozilla thumb */
input[type=range]::-moz-range-thumb {
  @mixin range-thumb;
}

/* Mozilla progress */
input[type=range]::-moz-range-progress {
  @mixin range-progress;
}

/* Mozilla thumb:focus */
input[type=range]:focus::-moz-range-thumb {
  @mixin range-thumb--focus;
}

/* IE thumb */
input[type=range]::-ms-thumb {
  margin-top: 0;

  @mixin range-thumb;
}

/* IE progress */
input[type=range]::-ms-fill-lower {
  @mixin range-progress;
}

/* IE thumb:focus */
input[type=range]:focus::-ms-range-thumb {
  @mixin range-thumb--focus;
}
