/* _variables.css */

:root {
  /* typography */
  --font-family--serif: "Merriweather";
  --font-family--sans-serif: "Merriweather Sans", system-ui;
  --font-family--system-ui: system-ui;
  --font-size--base: 1.5rem;
  --font-size--root: 10px;
  --font-size--input: 16px;
  --line-height--body: 1.5;
  --line-height--heading: 1.1;
  --line-height--input: 1;

  /* color palette */
  --blue: #3b71e8;
  --black: #444;
  --gray: #717171;
  --silver: #a8a8a8;
  --red: #dd2027;
  --lt-blue: #9db8f3;
  --lt-gray: #f6f6f6;

  /* state */
  --btn--default: var(--blue);
  --btn--active: #396bd6;
  --input--border--default: var(--gray);
  --input--border--error: var(--red);
  --input--border--focus: 0 0 0 3px var(--lt-blue);
  --input--background--default: var(--lt-gray);
  --input--background--focus: #fff;
  --input--background--error: #fadedf;
  --box-shadow--active: 0 4px 10px 0 rgba(77, 77, 77, 0.4);
  --box-shadow--focus: 0 0 0 3px var(--lt-blue);

  /* Rangeslider */
  --rangeslider--track-height: 3px;
  --rangeslider--thumb-diameter: 20px;
  --rangeslider--track-color: var(--silver);
  --rangeslider--fill-color: var(--blue);
  --rangeslider--thumb-color: var(--blue);
}

@custom-selector :--headings h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6;
