/* _widgets.css */

.drawer {
  background-color: #f6f6f6;
  border: 1px solid #f6f6f6;
  box-shadow: inset 0 0 1px rgba(168, 168, 168, 0.25);
  font-family: var(--font-family--system-ui);
  font-size: 12px;
  line-height: 1.1;
  margin: 5px 0 0;
  padding: 2rem 1rem;
}

input[type=checkbox].switch {
  margin: 0 0 0 auto;
  padding: 0;
  width: 25px;
}

.range-group {
  position: relative;
}

.range-group__tooltip {
  background-color: var(--gray);
  color: #fff;
  display: block;
  font-family: var(--font-family--system-ui);
  font-size: 12px;
  height: 20px;
  left: calc(-0.5 * var(--rangeslider--thumb-diameter));
  line-height: 1;
  padding: 4px 0;
  position: absolute;
  text-align: center;
  top: 5px;
  transform: translateX(var(--translateX));
  width: 40px;
}

.no-js .range-group__tooltip {
  display: none !important;
}

input[type=range]:active ~ .range-group__tooltip {
  background-color: var(--blue);
}
