/* _table.css */

.calculator table {
  font-family: var(--font-family--sans-serif);
  line-height: 1;
  margin-bottom: 3rem;
  margin-top: 3rem;
  width: 100%;
}

.calculator caption {
  font-weight: 700;
  text-align: left;
  text-decoration: underline;
}

.calculator td {
  background-color: #fff;
  padding: 1.5rem 0;
}

@media (min-width: 500px) {
  .calculator table {
    margin-top: 0.5rem;
  }

  .calculator td:first-of-type {
    padding-left: 2rem;
  }

  .calculator td:last-of-type {
    text-align: right;
  }
}
